.paralax {
  box-sizing: border-box;
  background: #222;
  padding: 0;
  margin: 0;
  font-family: "Sintony", sans-serif;
  font-weight: 300;
  font-size: 13px;
  text-transform: uppercase;
  color: #000;
}

.header {
  background-color: #fff;
  padding: 0;
  margin: 0;
}

.section {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100vh;
  letter-spacing: 4px;
  overflow: hidden;
  clip: rect(0, auto, auto, 0);
  .fixed {
    overflow: hidden;
    position: fixed;
    top: 50%;
    left: 50%;
  }
  .white {
    color: #fff;
  }
}

@for $i from 1 through 10 {
  .section:nth-child(#{$i}) {
    @if ($i==1) {
      background-color: #fff;
      color: #000;
      top: 0;
    } @else if($i==10) {
      background-color: #000;
      color: #fff;
      top: (100vh * ($i - 1));
    } @else {
      box-shadow: inset 0 1px 80px rgba(0, 0, 0, 0.14);
      background-color: hsl(200deg * $i, 90%, 40%);
      color: #fff;
      top: (100vh * ($i - 1));
    }
    z-index: ($i);
    .fixed {
      transform: translate(-50%, -50%);
    }
  }
}

