//FIRE
$black: #111217;
$brownl: #70392f;
$brownd: #612e25;
$yellow: #ffdc01;
$orange: #fdac01;
$red: #f73b01;
$animationtime: 1.5s;

body {
  .fire {
    z-index: 0;
    position: absolute;
    left: 6%;
    top: 5%;
    transform: translate(-50%, -25%);
    height: 7vw;
    width: 7vw;
    //background-color:$red;
    .flames {
      z-index: 0;
      position: absolute;
      bottom: 40%;
      left: 50%;
      width: 30%;
      height: 30%;
      //background-color:$red;
      transform: translateX(-50%) rotate(45deg);
      .flame {
        z-index: 0;
        position: absolute;
        right: 0%;
        bottom: 0%;
        width: 0%;
        height: 0%;
        background-color: $yellow;
        border-radius: 1vw;
        &:nth-child(2n + 1) {
          animation: flameodd $animationtime ease-in infinite;
        }
        &:nth-child(2n) {
          animation: flameeven $animationtime ease-in infinite;
        }
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: $animationtime/4;
        }
        &:nth-child(3) {
          animation-delay: $animationtime/4 * 2;
        }
        &:nth-child(4) {
          animation-delay: $animationtime/4 * 3;
        }
      }
    }
  }
}

@keyframes flameodd {
  0%,
  100% {
    width: 0%;
    height: 0%;
  }
  25% {
    width: 100%;
    height: 100%;
  }
  0% {
    background-color: $yellow;
    z-index: 1000000;
  }
  40% {
    background-color: $orange;
    z-index: 1000000;
  }
  100% {
    background-color: $red;
    z-index: -10;
  }
  0% {
    right: 0%;
    bottom: 0%;
  }
  25% {
    right: 1%;
    bottom: 2%;
  }
  100% {
    right: 150%;
    bottom: 170%;
  }
}

@keyframes flameeven {
  0%,
  100% {
    width: 0%;
    height: 0%;
  }
  25% {
    width: 100%;
    height: 100%;
  }
  0% {
    background-color: $yellow;
    z-index: 1000000;
  }
  40% {
    background-color: $orange;
    z-index: 1000000;
  }
  100% {
    background-color: $red;
    z-index: -10;
  }
  0% {
    right: 0%;
    bottom: 0%;
  }
  25% {
    right: 2%;
    bottom: 1%;
  }
  100% {
    right: 170%;
    bottom: 150%;
  }
}

